.color-button {
    -ms-flex-positive: 1;
        flex-grow: 1;
    border: none;
    padding: 0;
    background: none;
    outline: 0 none;
    cursor: pointer;
    margin-bottom: 5px;
}

.color-button:first-child {
    border-top-right-radius: 25%;
}

.color-button:last-child {
    border-bottom-right-radius: 20px;
}

.color-button:focus {
    outline: none;
}

.color-button-blue {
    background: -webkit-gradient(
        linear,
        left top, right top,
        from(rgb(0, 174, 255)),
        color-stop(25%, rgba(0, 174, 255, 0.3)),
        color-stop(50%, rgba(0, 174, 255, 0.2)),
        to(rgba(0, 174, 255, 0))
    );
    background: -webkit-linear-gradient(
        left,
        rgb(0, 174, 255) 0%,
        rgba(0, 174, 255, 0.3) 25%,
        rgba(0, 174, 255, 0.2) 50%,
        rgba(0, 174, 255, 0) 100%
    );
    background: -o-linear-gradient(
        left,
        rgb(0, 174, 255) 0%,
        rgba(0, 174, 255, 0.3) 25%,
        rgba(0, 174, 255, 0.2) 50%,
        rgba(0, 174, 255, 0) 100%
    );
    background: linear-gradient(
        to right,
        rgb(0, 174, 255) 0%,
        rgba(0, 174, 255, 0.3) 25%,
        rgba(0, 174, 255, 0.2) 50%,
        rgba(0, 174, 255, 0) 100%
    );
}

.color-button-green {
    background: -webkit-gradient(
        linear,
        left top, right top,
        from(rgb(70, 255, 53)),
        color-stop(25%, rgba(70, 255, 53, 0.3)),
        color-stop(50%, rgba(70, 255, 53, 0.2)),
        to(rgba(70, 255, 53, 0))
    );
    background: -webkit-linear-gradient(
        left,
        rgb(70, 255, 53) 0%,
        rgba(70, 255, 53, 0.3) 25%,
        rgba(70, 255, 53, 0.2) 50%,
        rgba(70, 255, 53, 0) 100%
    );
    background: -o-linear-gradient(
        left,
        rgb(70, 255, 53) 0%,
        rgba(70, 255, 53, 0.3) 25%,
        rgba(70, 255, 53, 0.2) 50%,
        rgba(70, 255, 53, 0) 100%
    );
    background: linear-gradient(
        to right,
        rgb(70, 255, 53) 0%,
        rgba(70, 255, 53, 0.3) 25%,
        rgba(70, 255, 53, 0.2) 50%,
        rgba(70, 255, 53, 0) 100%
    );
}

.color-button-red {
    background: -webkit-gradient(
        linear,
        left top, right top,
        from(rgb(255, 41, 0)),
        color-stop(25%, rgba(255, 41, 0, 0.3)),
        color-stop(50%, rgba(255, 41, 0, 0.2)),
        to(rgba(255, 41, 0, 0))
    );
    background: -webkit-linear-gradient(
        left,
        rgb(255, 41, 0) 0%,
        rgba(255, 41, 0, 0.3) 25%,
        rgba(255, 41, 0, 0.2) 50%,
        rgba(255, 41, 0, 0) 100%
    );
    background: -o-linear-gradient(
        left,
        rgb(255, 41, 0) 0%,
        rgba(255, 41, 0, 0.3) 25%,
        rgba(255, 41, 0, 0.2) 50%,
        rgba(255, 41, 0, 0) 100%
    );
    background: linear-gradient(
        to right,
        rgb(255, 41, 0) 0%,
        rgba(255, 41, 0, 0.3) 25%,
        rgba(255, 41, 0, 0.2) 50%,
        rgba(255, 41, 0, 0) 100%
    );
}




