.awesome-lightsaber-wrapper {
    text-align: center;
    height: 100%;
    position: relative;
}

@-webkit-keyframes flicker {
    0% {opacity: 1;}
    50% {opacity: 0.85;}
    100% {opacity: 1;}
}

@keyframes flicker {
    0% {opacity: 1;}
    50% {opacity: 0.85;}
    100% {opacity: 1;}
}

@-webkit-keyframes unsheathe {
    0% { height: 26.4%; }
    100% { height: 100%; }
}

@keyframes unsheathe {
    0% { height: 26.4%; }
    100% { height: 100%; }
}

.lightsaber-tilted {
    height: 26.4%;  /* at 26.4% there is no blade, set to 100% with animation via js*/
    width: 100%;
    -webkit-transform-origin: center bottom;
        -ms-transform-origin: center bottom;
            transform-origin: center bottom;
    z-index: 1000;
    pointer-events: none;
    position: absolute;
    bottom: 0;
    overflow: hidden;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}


.lightsaber-unsheathe {
    position: absolute;
    bottom: 0;
    height: 100%;  /* set from js to document.body.clientHeight, vh units are bad on mobile */
    width: 100%;
    overflow: hidden;
}

.lightsaber-img {
    height: 105%;
    width: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-animation: flicker 150ms linear infinite;
            animation: flicker 150ms linear infinite;
    -webkit-transform: scale(1.38, 1);
        -ms-transform: scale(1.38, 1);
            transform: scale(1.38, 1);
    -o-object-fit: contain;
       object-fit: contain;
}

.lightsaber-red {
    -webkit-filter: hue-rotate(250deg);
            filter: hue-rotate(250deg);
}

.lightsaber-blue {
    -webkit-filter: hue-rotate(80deg);
            filter: hue-rotate(80deg);
}

.lightsaber-green {
    /* nothing, it's already green */
}


