@-webkit-keyframes impactFlash {
    0% { background-color: #111; }
    100% { background-color: #050505; }
}

@keyframes impactFlash {
    0% { background-color: #111; }
    100% { background-color: #050505; }
}

.app {
    height: 100%;
    position: relative;
    background-color: #050505;
    color: white;
    overflow: hidden; /* Hides the scrollbar */
}


